import styled from 'styled-components';

import { simpleFlex, container } from '../../styles/mixins';

export const HeaderStyles = styled.div`
  ${simpleFlex};
  margin-bottom: 50px;
  padding: 30px 0;
  border-bottom: 1px solid #122f41;

  img {
    display: table;
    margin: 0 auto;
    width: 200px;
  }
`;

export const ContentWrapper = styled.div`
  ${container}
`;

export const ItemsHeader = styled.div`
  ${simpleFlex}
  justify-content: space-between;
`;

export const InformItens = styled.div`
  div:first-child {
    text-align: right;
    margin-bottom: 30px;

    a {
      color: initial;

      button {
        cursor: pointer;
        border: 0;
        background-color: #058586;
        color: white;
        padding: 8px 30px;
        border-radius: 4px;
        transition: 0.4s ease all;

        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
`;