import styled from 'styled-components';

export const container = `
  width: 90%;
  max-width: 1200px;
  margin: 0px auto;
`;

export const simpleFlex = `
  display: flex;
  align-items: center;
`;

export const Container = styled.div`
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 30px;
`;

export const Form = styled.form`
  display: flex;
  flex-direction: row;

  input {
    flex: 1;
    border: 1px solid #eee;
    padding: 10px 15px;
    border-radius: 4px;
    margin-right: 10px;
  }
`;

export const SubmitButton = styled.button.attrs({
  type: 'submit',
  alt: 'Pesquisar',
})`
  background: #06838a;
  border: 0;
  padding: 0px 15px;
  border-radius: 4px;
  color: white;

  display: flex;
  justify-content: center;
  align-items: center;

  & [disabled] {
    cursor: not-allowed;
    opacity: 0.6;
  }
`;

export const LoadingContainer = styled.div`
  ${simpleFlex}
  justify-content: center;
  flex-direction: column;
  p {
    margin-top: 30px;
  }
  margin-top: 40px;
`;

export const InfosTitle = styled.div`
  margin-top: 40px;
  font-weight: bold;
`;

export const ContainerExcel = styled.div`
  ${simpleFlex}
  justify-content: space-between;
  div {
    background: #06838a;
    padding: 20px 15px;
    color: white;
    border-radius: 4px;

    p {
      margin-bottom: 10px;
    }
  }
`;